.empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
   }

   .empty_name {
    color: #fff;
   }
   .empty_status,
   .empty_info {
    padding: 1rem;
   }
   .empty_status {
    color: rgba(255, 255, 255, 0.9);
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
   }
   .empty_img {
    border-radius: 50%;
    margin: 2rem 0;
   }
   .empty_btn {
    padding: 1rem;
    margin: 1rem 0;
    font-weight: bold;
    font-size: 1.2rem;
    border-radius: 30px;
    outline: 0;
    border:none;
   }
   .empty_btn:hover {
    background: rgba(255, 255, 255, 0.7);
    cursor: pointer;
   }