@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: "Nunito Sans", sans-serif, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 font-weight: 400;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.user{
    display:flex;
    align-items: flex-start;
    padding:1rem;
}

.user:hover{
    background: rgba(0,0,0,0.2);
    cursor:pointer;
}

.user_img{
    width:50px;
    border-radius: 50%;
}

.user_details{
    display:none
}

/* not small devices */

@media (min-width: 576px) {
 .user_details {
    display: block;
    padding: 0 0 0 1rem;
 }
 .user_name {
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    font-size: 1rem;
 }
}
.sidebar{
    width: 80px;
    background-color: rgba(32,32,35,1);
    height:100%;
    border-right:1px solid rgba(189,189,192,0.1);
    -webkit-transition: width 0.3s;
    transition: width 0.3s;
    overflow-y: scroll;
}

@media (min-width:576px){
    .sidebar{
        width:320px;
        
    }
}
.main{
    flex:1 1;
    background-color: rgba(25,25,27,1);
    height: 100%;
}
.empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
   }

   .empty_name {
    color: #fff;
   }
   .empty_status,
   .empty_info {
    padding: 1rem;
   }
   .empty_status {
    color: rgba(255, 255, 255, 0.9);
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
   }
   .empty_img {
    border-radius: 50%;
    margin: 2rem 0;
   }
   .empty_btn {
    padding: 1rem;
    margin: 1rem 0;
    font-weight: bold;
    font-size: 1.2rem;
    border-radius: 30px;
    outline: 0;
    border:none;
   }
   .empty_btn:hover {
    background: rgba(255, 255, 255, 0.7);
    cursor: pointer;
   }
.chatWindow {
     display: flex;
     flex-direction: column;
     height: 100vh;
    }
.Header {
    padding: 1rem 2rem;
    border-bottom: 1px solid rgba(189, 189, 192, 0.2);
   }
   .header_name {
    color: #fff;
   }
.chats {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 85%;
    margin: 0 auto;
    overflow-y: scroll;
}

.chat {
    margin: 1rem 0;
    color: #fff;
    padding: 1rem;
    background: -webkit-gradient(linear, left top, right top, from(#1986d8), to(#7b9cc2));
    background: -webkit-linear-gradient(left, #1986d8, #7b9cc2);
    background: linear-gradient(90deg, #1986d8, #7b9cc2);
    max-width: 90%;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.chat.is-user-msg {
    margin-left: auto;
    background: #2b2c33;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

@media (min-width: 576px) {
    .chat {
        max-width: 60%;
        }
   }
.message {
    width: 90%;
    margin: 1rem auto;
   }
.message_input {
    width: 100%;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border: 0;
    border-radius: 10px;
    font-size: 1rem;
    outline: 0;
   }
.App {
  display: flex;
  height: 100vh;
  color: rgba(189,189,192,1)
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

